@import '../../../scss/mixins';

.inputText {
  @include hbFormType;
  height: 49px;
  color: #fff;
  font-weight: normal;
  background: var(--petrol-80);
  border: 0;
  border-radius: 10px;
  @media screen and (max-width: 480px) {
    height: 51px;
  }
}

.cardTitle {
  margin-bottom: 16px;
  text-align: left;
  white-space: pre-line;
  @media screen and (max-width: 860px) {
    margin-bottom: 30px;
  }
}

.inputText:focus {
  background-color: var(--petrol-80);
  color: #fff;
  outline: 0;
  box-shadow: none;
}

.inputText::placeholder {
  color: var(--nightblue-50);
}

.inputText:-webkit-autofill,
.inputText:-webkit-autofill:hover,
.inputText:-webkit-autofill:focus,
.inputText:-webkit-autofill,
.inputText:-webkit-autofill:hover,
.inputText:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  background-color: var(--petrol-80);
  -webkit-text-fill-color: #ffffff;
  box-shadow: var(--petrol-80);
  transition: background-color 5000000s ease-in-out 0s;
}

.inputPassword {
  margin-bottom: 0;
  position: relative;
  text-align: left;
  .inputTextPassword {
    @include hbFormType;
    height: 49px;
    color: #fff;
    font-weight: normal;
    background: var(--petrol-80);
    border: 0;
    border-radius: 10px;
    @media screen and (max-width: 480px) {
      height: 51px;
    }
  }

  .inputTextPassword:focus {
    background-color: var(--petrol-80);
    color: #fff;
    outline: 0;
    box-shadow: none;
    // border-color: var(--petrol-80);
  }

  .inputTextPassword:invalid {
    border: 1px solid var(--petrol-80);
  }

  .inputTextPassword::placeholder {
    color: var(--nightblue-50);
  }

  .inputTextPassword:-webkit-autofill,
  .inputTextPassword:-webkit-autofill:hover,
  .inputTextPassword:-webkit-autofill:focus,
  .inputTextPassword:-webkit-autofill,
  .inputTextPassword:-webkit-autofill:hover,
  .inputTextPassword:-webkit-autofill:focus,
  select:-webkit-autofill,
  select:-webkit-autofill:hover,
  select:-webkit-autofill:focus {
    background-color: var(--petrol-80);
    -webkit-text-fill-color: #ffffff;
    box-shadow: var(--petrol-80);
    transition: background-color 5000000s ease-in-out 0s;
  }

  .iconPassword {
    position: absolute;
    right: calc(0.375em + 0.1875rem);
    top: calc(28px - 0.5em);
    font-size: 20px;
    color: #fff;
    cursor: pointer;
  }
}

.email {
  text-align: left;
}

.label {
  @include h6;
  text-align: center;
  a {
    color: var(--aquamarin-80);
    cursor: pointer;
  }
  &.lbSignIn {
    margin-top: 16px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    & > span {
      width: 100%;
    }
    @media screen and (max-width: 860px) {
      & > span {
        width: auto;
      }
    }
  }
  @media screen and (max-width: 360px) {
    font-size: 15px;
  }
}

.forgotPassword {
  text-align: center;
  margin-bottom: 16px;
  font-size: 13px;
  font-weight: 500;
  color: var(--aquamarin-80);
}

.btnSubmit {
  @include hbButtonText;
  margin: 16px 0;
  padding: 0;
  width: 100%;
  height: 45px;
  @media screen and (max-width: 860px) {
    margin: 30px 0 16px 0;
  }
}

.register_link {
  color: var(--aquamarin-80);
  cursor: pointer;
  @media screen and (max-width: 860px) {
    margin-left: 6px;
  }
}

.header {
  background: var(--petrol-100);
  top: 0;
  border: 0;
  justify-content: center;
  margin-top: 23px;
}

.accept {
  text-align: left;
  .link {
    color: var(--aquamarin-80) !important;
  }
}

.btnSignUp {
  margin: 0 0 16px;
  @media screen and (max-width: 860px) {
    margin: 0 0 30px;
  }
}
.hbIcon {
  font-size: 22px;
  margin-right: 8px;
}

.formGroupActions {
  margin-bottom: 0;
}

.groupSignIn {
  & > div {
    & > button {
      margin: 0 0 16px 0;
    }
    &:last-child {
      & > button {
        margin: 0;
      }
    }
  }
}
